import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-uploader',
  templateUrl: './uploader.component.html',
  styleUrls: ['./uploader.component.scss']
})
export class UploaderComponent implements OnInit {

  fileName = '';
  files: File[] = [];
  pDate = this.getDateFormated(new Date())

  constructor(private http: HttpClient) { }

  ngOnInit(): void {
  }

  uploadFile(event: any): void {
    this.files = event.target.files;
  }

  sendFiles(): void {
    const formData = new FormData();
    for (const file of this.files) {
      formData.append("files", file);
    }
    this.sendFilesRequest(formData).subscribe(
      response => {
      },
      err => {
        console.log(err.message)
      }),
      () => {
        this.files = [];
      }

  }

  sendFilesRequest(files: FormData): Observable<HttpResponse<Object>> {
    let headerss = new HttpHeaders().set('uploadDate', this.pDate as string)
    console.log(headerss)
    return this.http.post('http://localhost:8080/upload', files, { observe: 'response', headers: headerss })
  }

  twoDigitsString(num: number): String {
    if (num > 9) {
      return `${num}`;
    } else {
      return `${'0' + num}`;
    }
  }

  getDateFormated(date: Date): String {
    return date.getFullYear() + '-' + this.twoDigitsString((date.getMonth() + 1)) + '-' + this.twoDigitsString(date.getDate());
  }

}
